/**
 * exiforientation, .
 * @param  {Number} orientation [description]
 * @return {[type]}             [description]
 */
export const getDegree = (orientation = 1) => {
  let deg = 0;

  //  - 
  // 1 - 0
  // 6 - 90
  // 8 - 270
  // 3 - 180
  switch (parseInt(orientation)) {
    case 6: {
      deg = 90;
      break;
    }
    case 8: {
      deg = -90;
      break;
    }
    case 3: {
      deg = 180;
      break;
    }
    default: {
      break;
    }
  }

  return deg;
};



