import qs from 'qs';
import { PARSER_QUERYSTRING } from '../../../constants/actionTypes';

/**
 * action, modalaction
 * @param {title} alert
 * @param {content} alert
 * @param {alertType} alert: , , .
 */
export function parser() {
  const value = qs.parse(window.location.search.substr(1));

  return {
    type: PARSER_QUERYSTRING,
    value
  };
}



