import { fromJS } from 'immutable';
import { PARSER_QUERYSTRING } from '../../../constants/actionTypes';
import { convertObjIn } from '../../../../../common/utils/typeConverter';

const initValues = fromJS({
  // my projects, .
  initGuid: -1,
  webClientId: 1,

  // book, .
  product: '',
  cover: '',
  leatherColor: '',
  size: '',
  paper: '',
  paperThickness: '',
  title: ''
});

/**
 * cookiesreducer, cookiestore.
 * @param state
 * @param action
 */
const qs = (state = initValues, action) => {
  switch (action.type) {
    case PARSER_QUERYSTRING:
      return state.merge(convertObjIn(action.value));
    default:
      return state;
  }
};

export default qs;



