import { fromJS, is } from 'immutable';

/**
 * immutablelist.
 * @param  {LIST}   array 
 * @param  {Function} fn    .
 * @return {LIST}        .
 */
export const unique = (array, fn) => {
  if (!array || array.size <= 1) {
    return array;
  }

  let newArr = fromJS([]);

  array.forEach((item) => {
    const index = newArr.findIndex((m) => {
      if (fn && typeof (fn) === 'function') {
        return fn(item, m);
      }

      return is(item, m);
    });

    if (index === -1) {
      newArr = newArr.push(item);
    }
  });

  return newArr;
};



