import { guid } from '../../../../common/utils/math';

/**
 * element
 */
export default class Element {
  constructor(options = {}) {
    const {
      id = guid(),

      // element
      type = 'PhotoElement',

      // element
      elType = 'image',

      // elementx
      x = 0,

      // elementy
      y = 0,

      // element
      width = 0,

      // element
      height = 0,

      // , 
      px = 0,
      py = 0,

      // , 
      pw = 1,
      ph = 1,

      // 
      dep = 0,

      // id
      imageid = '',
      encImgId = '',

      // 
      rot = 0,

      // .
      imgRot = 0,

      // 
      imgFlip = false,

      // , 
      cropLUX = 0,
      cropLUY = 0,

      // , 
      cropRLX = 0,
      cropRLY = 0,

      // .
      border = {
        color: '#000000',
        size: 0,
        opacity: 100
      },

      // .
      lastModified = Date.now()
    } = options;

    this.id = id || guid();

    // element
    this.type = type;

    // element
    this.elType = elType;

    // elementx
    this.x = x;

    // elementy
    this.y = y;

    // element
    this.width = width;

    // element
    this.height = height;

    // , 
    this.px = px;
    this.py = py;

    // , 
    this.pw = pw;
    this.ph = ph;

    // 
    this.dep = dep;

    // id
    this.imageid = imageid;
    this.encImgId = encImgId;

    // .
    this.rot = rot;
    this.imgRot = imgRot;

    // 
    this.imgFlip = imgFlip;

    // , 
    this.cropLUX = cropLUX;
    this.cropLUY = cropLUY;

    // , 
    this.cropRLX = cropRLX;
    this.cropRLY = cropRLY;

    // element.
    this.border = border;

    // .
    this.lastModified = lastModified;
  }
}



