import { combineReducers } from 'redux';
import qs from './queryStringReducer';
import urls from './urlsReducer';
import userInfo from './userInfoReducer';
import albumId from './albumIdReducer';

// reducer, reducer.
export default combineReducers({
  qs,
  urls,
  userInfo,
  albumId
});



