import { forIn, get } from 'lodash';

/**
 * base url, query string, url.
 * @param {string} base 
 * @param {string} api 
 * @param {object} qs query string
 * @returns {string} url
 */
export const combine = (base, api, qs) => {
  if (!base || !api) {
    return '';
  }
  let aBase = base;
  if (aBase.endsWith('/')) {
    aBase = aBase.slice(0, aBase.length - 1);
  }

  let params = [];
  if (qs) {
    forIn(qs, (v, k) => {
      params.push(`${k}=${v}`);
    });
  }

  return `${aBase}/${api}?${params.join('&')}`;
};

/**
 * objectkeyvalue.
 * @param obj
 * @param path
 * @returns {*|string}
 */
export const getUrl = (obj, path) => {
  return get(obj, path) || '';
};

/**
 * url
 * @param  name
 * @return {string}
 */
export const getUrlParam = (name) => {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) return unescape(r[2]);
    return "";
};



