import { UPDATE_RATIO } from '../../../constants/actionTypes';

/**
 * workspacepreviewratio
 * @param ratios  : [{type, ratio}]: workspacepreviewratio
 */
export function updateRatio(ratios) {
  return {
    type: UPDATE_RATIO,
    ratios
  };
}




