import { forIn, merge } from 'lodash';
import qs from 'qs';

/**
 * base url, query string, url.
 * @param {string} base 
 * @param {string} api 
 * @param {object} qs query string
 * @returns {string} url
 */
export function combine(base, api, qs) {
  if (!base) {
    return '';
  }

  const params = [];
  if (qs) {
    forIn(qs, (v, k) => {
      params.push(`${k}=${v}`);
    });
  }

  return api ? `${base}/${api}?${params.join('&')}`
             : `${base}?${params.join('&')}`;
}

export function getQueryStingObj() {
  const o = {
    // my projects, .
    initGuid: -1,
    webClientId: 1,

    // book, .
    product: '',
    size: '',
    paper: '',
    title: '',
    category: '',
    frameStyle: '',
    color: '',
    metalType: '',
    matte: '',
    matteStyle: '',
    glassStyle: '',
    canvasBorder: '',
    finish: '',
    canvasBorderSize: '',
    orientation: ''
  };

  const obj = qs.parse(window.location.search.substr(1));

  // project id.
  // if (typeof obj.mainProjectUid === 'string' && !isNaN(obj.mainProjectUid)) {
  //   obj.initGuid = parseInt(obj.mainProjectUid);
  // }

  return merge({}, o, obj);
}



